<template>
  <div class="container-app">
    <div class="container-quiz">
      <div class="quiz-header">
     <h1>Digex Quiz App</h1>
      </div>
      <div class="quiz-main" v-for="(element,index) in questions.slice(a,b)" :key="index" v-show="quiz">
        <div class="box-question">
          <h2>Question {{b}}/{{questions.length}} </h2>
          <p> {{element.question}} </p>
        </div>
        <div class="box-suggestions">
          <ul>
            <li v-for="(item,index) in element.suggestions" :key="index" :class="select ? check(item) : ''" @click="selectResponse(item)"> {{item.suggestion}} </li>
          </ul>
        </div>
      </div>
      <div class="box-score" v-if="score_show">
        <h2>Votre score </h2>
        <h2> {{score}}/{{questions.length}} </h2>
        <div class="btn-restart">
          <button @click="restartQuiz()">Reessayer <i class="fas fa-sync-alt"></i> </button>
        </div>
      </div>
      <div class="quiz-footer">
        <div class="box-button">
          <button @click="skipQuestion()" >Passer</button>
          <button @click="nextQuestion()" >Suivant</button>
        </div>
      </div>
    </div>
  </div>
</template>



<script>
export default {
data(){
  return{
    questions:[
      {
        question:'Quelle est la capitale de la russie?',
        suggestions:[
          {suggestion :'Cameroun'},
          {suggestion :'Dakar'},
          {suggestion :'Paris'},
          {suggestion :'Moscow',correct:true},
        ]
      },
       {
        question:'Quelle est la capitale de Andore?',
        suggestions:[
          {suggestion :'Andoras'},
          {suggestion :'Caracas'},
          {suggestion :'Gambas'},
          {suggestion :'Andore la vielle',correct:true},
        ]
      },
       {
        question:'Qui est le directeur de digex?',
        suggestions:[
          {suggestion :'Content'},
          {suggestion :'Poli'},
          {suggestion :'Impatient'},
          {suggestion :'Patient',correct:true},
        ]
      },
       {
        question:'Quelle est la capitale de la turquie?',
        suggestions:[
          {suggestion :'Ankara',correct:true},
          {suggestion :'Bolivie'},
          {suggestion :'Talin'},
          {suggestion :'Yaounde'},
        ]
      },
    ],
    a:0,
    b:1,
    select:false,
    score:0,
    quiz:true,
    score_show:false,
    next:false,
  }
},

methods :{
  selectResponse(e){
    this.select = true
    this.next = true
    if (e.correct) {
      this.score++
    }
  },

  check(status){
    if (status.correct) {
      return 'correct'
    }
    else{
     return 'incorrect'
    }
  },
  nextQuestion(){
    if (this.questions.length - 1 == this.a) {
      this.score_show = true
      this.quiz = false
    }
    else{
      this.a++;
      this.b++
      this.select = false
    }
    
  },
  skipQuestion(){
    if (this.questions.length - 1 == this.a) {
      this.score_show = true
      this.quiz = false
    }
     else{
      this.a++;
      this.b++
    }
  },
  restartQuiz(){
    Object.assign(this.$data, this.$options.data())
  }
}


}
</script>



